@use "abstracts/mixins/transition-properties" as *
@use "abstracts/mixins/breakpoints" as breakpoint

$header-height: 5rem
$viewport-padding: var(--fluid-space-md)

.page-wrapper
  display: grid
  grid-template-rows: $header-height 1fr auto
  grid-template-areas: "header" "main" "footer"
  position: relative
  min-height: 100dvh

.main-content
  grid-area: main
  padding: $viewport-padding
  overflow-x: hidden
  max-width: 100dvw

.container
  width: 100%
  max-width: clamp(var(--min-width, 37.5rem), 90%, var(--max-width, 75rem))
  margin-inline: auto

.content-wrapper
  width: 100%
  max-width: 65ch
  margin-inline: auto
  padding-inline: var(--space-base)
