@use "abstracts"
@use "base"
@use "components"
@use "layout"
@use "pages"
@use "vendors"

@use "abstracts/mixins/transition-properties" as *
@use "abstracts/mixins/breakpoints" as breakpoint

.header-drawer
  position: fixed
  inset: 5rem 0.5rem 0.5rem
  z-index: 999
  box-shadow: var(--box-shadow-md)
  box-sizing: border-box
  overflow-y: auto
  background: linear-gradient(to top, hsl(from var(--color-sky-subtle) h s l / .925), var(--color-sky-subtle))
  color: var(--color-fg-accent-on-emphasis)
  transform: translateX(25%)
  opacity: var(--opacity-none)
  transition: transform 0.4s cubic-bezier(0.17, 0.67, 0.51, 1), opacity 0.3s cubic-bezier(0.17, 0.67, 0.51, 1)
  pointer-events: none

  .drawer-content
    display: flex
    flex-direction: column
    gap: var(--space-md)
    padding: var(--fluid-space-sm)
    overflow-y: auto

  .drawer-link
    color: inherit
    padding: var(--space-md) var(--space-base)
    display: inline-flex
    align-items: center
    gap: var(--space-sm)
    border-radius: var(--border-radius-default)
    font-size: var(--font-size-sm)
    text-decoration: none

[data-drawer="open"] .header-drawer
  transform: translateY(0)
  opacity: var(--opacity-full)
  pointer-events: auto

.drawer-mask
  position: fixed
  inset: 0
  background-color: var(--color-bg-overlay-backdrop)
  z-index: 998
  opacity: 0
  pointer-events: none
  transition: opacity 0.3s ease

[data-drawer="open"] .drawer-mask
  opacity: 1
  pointer-events: auto
